.nav {

  &__menu-opener {
    justify-self: start;
    grid-column: 1 / 2;
    grid-row: 1 / 1;
  }

  &__logo {
    font-weight: bold;
    font-size: 30px;
    line-height: 100%;
    height: 61px;
    width: max-content;
    grid-row: 1 / 1;
    grid-column: 2 / 3;

    @include onTablet {
      font-size: 36px;
      height: 72px;
      grid-column: 4 / 6;
    }

    @include onDesktop {
      grid-column: 6 / 8;
    }
  }

  &__link {
    color: #0f0e08;
  }
}
